<template>
  <div>
    <h3 class="mb-2">Toko Aralia</h3>
    <vs-table search :data="toko">
      <template slot="header" class="mb-5">
        <vs-button icon="queue" color="primary" type="filled" size="small" @click="addLaporan=true">Laporan</vs-button>
      </template>
      <template slot="thead">
        <vs-th sort-key="tanggal" class="bg-primary text-white">
         Tanggal
        </vs-th>
        <vs-th sort-key="status" class="bg-primary text-white">
          Status Toko
        </vs-th>
        <vs-th sort-key="status-it" class="bg-primary text-white">
          Status IT
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="items.id" v-for="items in data" >
          <vs-td :data="items.tanggal">
            {{items.tanggal}}
          </vs-td>

          <vs-td :data="items.status_toko">
                <vs-button  v-if="items.status_toko == 'Y'" color="success" type="filled" size="small" @click="detailLaporan=true">Cocok</vs-button>
                <vs-button  v-else color="danger" type="filled" size="small" @click="detailLaporan=true">Tidak Cocok</vs-button>
          </vs-td>

          <vs-td :data="items.status_it">
                <vs-button  v-if="items.status_it == 'Y'" color="success" type="filled" size="small" @click="detailLaporan=true">Cocok</vs-button>
                <vs-button  v-else color="danger" type="filled" size="small" @click="detailLaporan=true">Tidak Cocok</vs-button>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-popup class="holamundo" title="Tambah Laporan" :active.sync="addLaporan">
        <div class="centerx labelx">
            <vs-input class="mb-5 w-full" label="POS" v-model="laporan.penjualan"/>
            <vs-input class="mb-6 w-full"  label="Manual" v-model="laporan.pengeluaran"/>
            <vs-textarea class="mb-5 w-full" label="Alasan Kenapa Tidak Balance"  height="150px" v-model="laporan.alasan" />
        </div>
        <div class="text-right">
        <vs-button color="success" type="filled" >Kirim</vs-button>
        </div>
    </vs-popup>
    <vs-popup class="holamundo" title="Detail Laporan" :active.sync="detailLaporan">
        <div class="centerx labelx">
            <vs-input class="mb-5 w-full" label="POS" disabled v-model="detailChecksheet.pos"/>
            <vs-input class="mb-6 w-full"  label="Manual" disabled v-model="detailChecksheet.manual"/>
            <vs-input class="mb-6 w-full"  label="Admin" disabled v-model="detailChecksheet.admin"/>
            <vs-textarea class="mb-5 w-full" label="Alasan Kenapa Tidak Balance" disabled height="150px" v-model="detailChecksheet.alasan" />
            <vs-textarea class="mb-5 w-full" label="Koresksi IT" disabled height="100px" v-model="detailChecksheet.koreksi" />
        </div>
    </vs-popup>
  </div>
</template>

<script>
export default {
  data(){
      return{
        tokoDetails:[],
        addLaporan:false,
        detailLaporan:false,
        laporan: {
          penjualan:'',
          penngeluaran:'',
          alasan:''
        },
        detailChecksheet:{
          pos: '2.500.000',
          manual: '2.500.000',
          admin:'2.500.000',
          alasan:'',
          koreksi:''
        },
        toko:[
            {
              "id": 1,
              "tanggal": "10-06-2020",
              "status_toko": "Y",
              "status_it" :"T"
            },
            {
              "id": 2,
              "tanggal": "11-06-2020",
              "status_toko": "Y",
              "status_it" :"Y"
            },
        ]
      }
  },

  mounted(){
    this.$http.get('https://reqres.in/api/users/',{
            params:{
                id: this.$route.params.id
            }
        })
        // .then(res => (console.log(res.data)))
        .then(res => {
          this.tokoDetails = res.data.data
          // console.log(this.tokoDetails);
        })
        // .catch(err => console.log(err))
  },

  // methods:{
  //   setDataToko(data){
  //     this.tokoDetail = data
  //   }
  // }
}
</script>